@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);

body{
  font-family:"AudioWide";
  margin:0;
  -ms-scroll-chaining:none;
      overscroll-behavior:none;
}
#pad-wrapper{
    width:100%;
    max-width:120px;
    height:120px;
    text-align:center;
    background-color:black;
    border-radius:50%;
    background-color:#ffdd22;
    display:flex;
    justify-content:center;
    align-items:center;
    margin:9px 0;
}
.pad{
    max-width:96%;
    width:100%;
    height:96%;
    background:radial-gradient(#333, #070707);
    border-radius:50%;
    text-align:center;
    line-height:120px;
    font-size:25px;
    color:rgba(0,0,0,0);
    cursor:pointer;
}
.pad:hover{
    color:white;
}

@media only screen and (max-width: 820px) {
    #pad-wrapper{
        width:70px;
        height:70px;
    }
    .pad{
        line-height:65px;
    }
}



#side-panel{
    display:flex;
    flex-direction:column;
}
#side-panel-wrapper{
    display:flex;
    flex-direction:column;
    justify-content:space-evenly;
    align-items:center;
    font-size:20px;
    text-align:center;
    height:55%;
}
#machine-name{
    font-size:25px;
    font-style:italic;
    font-weight:900;
    margin:0 0 1em 0;
}
.button-wrapper{
    background-color:#ffdd22;
    width:75px;
    height:36px;
    text-align:center;
    border-radius:3px;
    margin:5px 30px;
    border:2px solid #333;
    cursor:pointer;
}
.button{
    width:49%;
    height:97%;
    background-color:white;
    border-radius:3px;
}
#buttons{
    display:flex;
    margin:.5em 0 .8em 0;
}
#display{
    background-color:#ffdd22;
    height:35px;
    width:280px;
    border-radius:3px;
    font-size:25px;
    color:black;
    border:2px solid #333;
}
input[type=range] {
    -webkit-appearance:none;
    background:black;
    height:20px;
    width:240px;
    border-radius:8px;
    outline:none;
    cursor:pointer;
}
#speaker{
    margin-top:.5em;
    width:100%;
    line-height:1em;
}
hr{
    background-color:black;
    height:9px;
    border:none;
    margin:1em 0;
    border-radius:5px;
}

@media only screen and (max-width:820px) {
    #machine-name{
        display:none;
    }
    .button-wrapper{
       height:25px;
       width:40px;
       border:1px solid #333;
    }
    input[type=range]{
        width:250px;
    }
    hr{
        margin:.5em;
        width:430px;
    }
    #display{
        border:1px solid #333;
        font-size:1.5em;
        height:30px;
        width:250px;
    }
    #side-panel-wrapper{
        font-size:.9em;
    }
}
#page-wrapper{
  background-color:#2d545e;
  width:100%;
  height:100vh;
  color:white;
  display:flex;
  align-items:center;
  justify-content:center;
}
#drum-machine-wrapper{
  display:flex;
  justify-content:space-between;
  flex-wrap:wrap;
  align-items:center;
  flex-wrap:wrap;
  background-color:#111;
  width:100%;
  max-width:830px;
  height:470px;
  box-shadow:5px 5px 8px 3px #222;
  border-radius:3px;
}
#pad-outer{
  max-width:55%;
  width:100%;
  height:85%;
  display:flex;
  flex-wrap:wrap;
  align-items:center;
  justify-content:space-evenly;
  margin-bottom:2.7em;
}
#side-panel-outer{
  max-width:41%;
  width:100%;
  margin:1em 2em 0 0;
  height:100%;
  align-self:flex-start;
  display:flex;
  justify-content:center;
}
#name-mobile{
  display:none;
  font-style:italic;
}

@media only screen and (max-width:820px){
  #drum-machine-wrapper{
    height:600px;
    width:500px;
    justify-content:center;
    flex-direction:column;
  }
  #name-mobile{
    display:inline;
    display:initial;
    align-self:flex-end;
    margin:.7em 1.5em 0 0;
  }
  #pad-outer{
    height:40%;
    margin-top:.7em;
  }
  #side-panel-outer{
    width:100%;
    height:40%;
    align-self:center;
    margin:auto;
    margin-top:.5em;
  }
}
